import React from 'react';
import './ProductColorView.css';

const ProductColorView = ({color}) => {



  let selectedColor;
  switch (color) {
    case "":
      selectedColor = "black";
      break;
    case "":
      selectedColor = "white";
      break;
    case "":
      selectedColor = "blue";
      break;
    case "":
      selectedColor = "beige";
      break;
    case "":
      selectedColor = "turquoise";
      break;
    case "":
      selectedColor = "burgundy";
      break;
    case "":
      selectedColor = "lightblue";
      break;
    case "":
      selectedColor = "yellow";
      break;
    case "":
      selectedColor = "green";
      break;
    case "":
      selectedColor = "gold";
      break;
    case "":
      selectedColor = "coral";
      break;
    case "":
      selectedColor = "brown";
      break;
    case "":
      selectedColor = "red";
      break;
    case "":
      selectedColor = "orange";
      break;
    case "":
      selectedColor = "pink";
      break;
    case "":
      selectedColor = "grey";
      break;
    case "":
      selectedColor = "multi";
      break;
    default:
      selectedColor = "multi"
  }





  return (
    <div className={`product-color-view bg_${selectedColor}`}>
    </div>
  )
};

export default ProductColorView;
